.App {
  margin-top: 24px;
}

.input {
  height: 44px;
  margin-left: 24px;
  border: 1px solid transparent;
  border-radius: 16px;
  box-shadow: 0 1px 3px rgb(23 23 23 / 24%);
  width: 650px;
  border-radius: 16px;
  padding-left: 16px;
  outline: none;
  font-size: 16px;
}

.search-icon {
  color: #8ab4f8;
  position: absolute;
  left: 650px;
  top: 36px;
  font-size: 24px;
  cursor: pointer;
}

.icon {
  font-size: 16px;
  cursor: pointer;
  color: #fff;
  padding: 10px;
  border-radius: 50%;
  height: 10px;
  width: 10px;
  align-items: center;
  text-align: center;
  display: flex;
  background: black;
  background: rgba(32, 33, 36, 0.6);
  margin-right: 12px;
}

.image-container {
  display: flex;
  flex-flow: wrap;
  margin-top: 24px;
  margin-left: 24px;
  height: calc(100vh - 120px);
  overflow: auto;
}

.author {
  color: #3c4043;
  font-size: 12px;
  line-height: 20px;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 200px;
}

.link {
  font-size: 11px;
  line-height: 16px;
  color: #5f6368;
  cursor: pointer;
  height: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: 0;
  width: 200px;
}
.link:hover {
  text-decoration: underline;
}
/* Spinner CSS */
.spinner-container {
  display: flex;
  margin: auto;
  justify-content: center;
}

.spinner {
  border: 2px solid #f3f3f3;
  border-top: 2px solid #3498db;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.icon-container {
  display: flex;
  position: absolute;
  width: 576px;
  margin: 12px;
}

.image-viewer {
  padding: 24px;
  background: "black";
}

.image-box {
  margin: auto;
  height: 235px;
  margin-bottom: 24px;
}
.small-img:hover {
  box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
  border-radius: 6px;
}

.img {
  max-height: 500px;
  max-width: 600px;
  cursor: pointer;
}

.placeholder {
  height: 200px;
  background-repeat: no-repeat;
  background-size: contain;
}

.small-img {
  max-height: 200px;
  max-width: 300px;
  cursor: pointer;
}

.action {
  display: flex;
  justify-content: space-between;
}

.button {
  background-color: #8ab4f8;
  font-size: 14px;
  height: 36px;
  border-radius: 36px;
  width: 60px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
}

.button:hover {
  text-decoration: underline;
}
